#head {
  position: absolute;
  z-index: 20;
  top: 0px;
  left: 0px;
  right: 0px;
  background: #ffffff;

  -webkit-box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);

  padding: 0px 0px;
  height: 55px;
}

#head .logo {
  /*display:block;
	width: 190px;
	height: 30px;
	background: url(../img/imgs.png) no-repeat -36px 0px;
	float:left;
	margin-top: 10px;*/
  display: block;
  width: 281px;
  height: 75px;
  position: absolute;
  left: -2%;
  margin-left: 168px;
  top: -10px;
}

#head .greenpeace_logo {
  display: block;
  width: 170px;
  height: 75px;
  position: absolute;
  left: -2%;
  margin-left: -2px;
  top: -10px;
}

#head .profile {
  float: right;
  position: relative;
  margin-left: 20px;
}

#head .profile .avatar {
  display: block;
  overflow: hidden;
  width: 53px;
  height: 53px;
  -webkit-border-radius: 26px;
  -moz-border-radius: 26px;
  border-radius: 26px;
}

#head .profile .avatar img {
  display: block;
  max-width: 100%;
}

#head .show_city {
  width: 40px;
  height: 32px;
  float: left;
  margin-top: 10px;
  display: none;
}

#head .show_city.active {
  width: 32px;
  height: 20px;
  background-position: -89px -151px;
  margin-top: 17px;
}

.callout {
  position: absolute;
  width: 248px;
  background: #ffffff;
  margin-top: 25px;
  right: 0px;

  display: none;
}

.callout .callout_head,
.callout .callout_footer {
  background: #f2f2f2;
}

.callout .callout_head_title {
  color: #303030;
  font-size: 20px;
  font-weight: 300;
  height: 35px;
  line-height: 35px;
  padding: 20px 30px;
  text-transform: uppercase;
}

.callout .callout_footer {
  padding: 10px 20px;
}

.callout ul {
  /*display:block;*/
}

.callout li {
  display: block;
  padding: 7px 0px;
  border-bottom: 1px solid #ededed;
}

.callout li a {
  display: block;
  font-size: 16px;
  color: #303030;
  padding-left: 23px;
}

.callout li a:hover {
  color: #000000;
}

.callout:before {
  content: "";
  position: absolute;
  display: block;
  border: 10px solid transparent;
  border-bottom: 10px solid #f2f2f2;
  bottom: 100%;
  right: 16px;
}

#head .search_box {
  float: right;
  position: relative;
  margin: 13px 40px 0px 40px;
}

#head .search_box input {
  height: 30px;
  border-bottom: 1px solid #979797;
  padding: 0px 5px;
  font-style: italic;
}

#head .search_btn {
  position: absolute;
  display: block;
  width: 19px;
  height: 20px;
  right: 0px;
  top: 0px;
  opacity: 0.5;
}

#head .search_btn:hover {
  opacity: 1;
}

#head .top_menu {
  float: right;
  display: block;
  font-size: 16px;
  margin-top: 20px;
}

#head .top_menu li {
  display: inline-block;
}

#head .top_menu li+li {
  padding-left: 50px;
}

#head .top_menu li a {
  color: #303030;
}

#head .top_menu li a:hover {
  color: #000000;
}

#head .callout .callout_head .avatar {
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  border-radius: 0px;
  width: 80px;
  height: 80px;
  display: inline-block;
  margin-right: 13px;

  overflow: hidden;
  position: relative;
  cursor: pointer;
}

#head .callout .callout_head .avatar input,
.edit_profile_page .avatar input {
  width: 200%;
  height: 200%;
  position: absolute;
  top: 0px;
  left: 0px;

  opacity: 0;
  z-index: 99;

  cursor: pointer;
}

#head .callout .callout_head .avatar:before,
.edit_profile_page .avatar:before {
  content: "";
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  z-index: 8;
  background: rgba(54, 59, 85, 0.75);
  opacity: 0;

  -webkit-transition: opacity 0.5s;
  -moz-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

#head .callout .callout_head .avatar:after,
.edit_profile_page .avatar:after {
  content: "";
  width: 30px;
  height: 37px;
  display: block;
  position: absolute;
  z-index: 9;
  left: 50%;
  top: 50%;

  margin-left: -15px;
  margin-top: -20px;

  opacity: 0;

  -webkit-transition: opacity 0.5s;
  -moz-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

#head .callout .callout_head .avatar:hover:before,
#head .callout .callout_head .avatar:hover:after,
.edit_profile_page .avatar:hover:after,
.edit_profile_page .avatar:hover:before {
  opacity: 1;
}

#head .callout .callout_head .profile_name {
  display: inline-block;
  overflow: hidden;
  height: 65px;
  padding-top: 15px;
  color: #303030;
  font-size: 16px;
  /*width: 100px;*/
}

#head .callout .callout_head .profile_name span {
  font-size: 14px;
  color: #707070;
  display: block;
}

.logout {
  position: relative;
  padding-right: 48px;
  line-height: 30px;
  text-decoration: none;
  color: #707070;
  font-size: 14px;
  display: inline-block;
}

.logout:before {
  content: "";
  display: block;
  position: absolute;
  width: 30px;
  height: 30px;
  right: 0px;
  top: 0px;
  opacity: 0.4;
}

.logout:hover {
  color: #000000;
}

.logout:hover:before {
  opacity: 1;
}