
#footer {
  position: absolute;
  bottom: 0px;
  left: 0px;
  right: 0px;
  height: 40px;
  background: rgba(0, 0, 0, 0.5);
  font-size: 16px;
  padding: 10px 30px;
  text-align: right;
}

#footer ul {
  display: block;
}

#footer li {
  display: inline-block;
  padding: 0px 20px;
}

#footer li a {
  color: #ffffff;
  line-height: 20px;
}

#footer li a:hover {
  text-decoration: underline;
}


#list{
	max-width:100%;
	overflow:hidden;
	min-height:100%;
	min-width: 320px;
	
	position:absolute;
	left: 0px; right: 0px;
	top: 0px; bottom: 0px;
}
.list{
	max-width:96%;
	margin: 0 auto;
	position:relative;
}
.list_size{
	max-width:100%;
	width: 1200px;
	margin: 0 auto;
	position:relative;	
}